	module.exports = {"data":{"post":{"html":"<p>For our return flight to Germany next week, not only do we have to worry about flight attendants creating <a href=\"http://www.nwaafa.org/default.asp?id=184\" title=\"CHAOS - Create Havoc Around Our System\">CHOAS</a> for Northwest Airlines but now we can no longer <a href=\"http://www.startribune.com/462/story/606764.html\" title=\"More passenger screening coming to MSP starting Friday\">carry liquids</a> in our carry-on luggage. It seems you don't actually have to commit a terrorist act to <a href=\"http://news.bbc.co.uk/2/hi/europe/4778931.stm\">disrupt airline travel</a>, merely talking about it within earshot of a police wiretap seems to be enough. But it could be <a href=\"http://www.educatedguesswork.org/movabletype/archives/2006/08/threat_modellin_1.html\">worse</a> (scroll to first comment)...</p>\n<blockquote><p>At least Richard Reed didn't strap C4 to his crotch, I'd hate to see what that would have done to the airport lines.</p></blockquote>\n<p>Ouch.</p>","fields":{"title":"It could be worse, it could be raining...","date":"11 Aug 2006","url":"https://papascott.de/2006-08-11-it-could-be-worse-it-could-be-raining/"}}},"pathContext":{"slug":"/2006-08-11-it-could-be-worse-it-could-be-raining/","prev":{"fields":{"title":"Wherever we go, that's where we are","date":"04 Aug 2006","slug":"/2006-08-04-wherever-we-go-thats-where-we-are/"}},"next":{"fields":{"title":"My Brother Dave","date":"11 Aug 2006","slug":"/2006-08-11-my-brother-dave/"}}}}

